@import '~node_modules/modern-normalize/modern-normalize.css';

.is-hidden{
    display: none;
}
.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: $bg-light;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
blockquote,
cite,
p {
    margin: 0;
}

.list {
    list-style-type: none;
    padding-inline-start: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    color: inherit;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.container {
    margin: 0 auto;
    padding-left: 16px;
    padding-right: 16px;

    @media screen and (max-width: 767px) {
        max-width: 480px;
    }

    @media screen and (min-width: 768px) {
        width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }

    @media screen and (min-width: 1200px) {
        width: 1200px;
        padding-left: 16px;
        padding-right: 16px;
    }
}
